      },
    },
    mounted(){
      // data = await this.GET_ITEMS_INFO_BY_ID({class: this.itemsListNow[i].classid, instance: this.itemsListNow[i].instanceid});
            // if (data) {
              
            //   this.itemsListNow[i].market_hash_name = data.market_name;
            //   this.$set(this.itemsListNow[i], 'CURRENT_PRICE', data.min_price);
            // }
        


    },  
    methods:{
     async add(){
       var data = await this.GET_ITEMS_INFO_BY_ID({class: 5268244156, instance: 188530139});
       console.log(data);
       var data1 = await this.GET_ITEMS_INFO_BY_ID({class: 5268244156, instance: 188530139});
       console.log(data1);
       
      },  
      ...mapActions(['GET_ITEMS_INVENTORY', 'ADD_ITEM_IN_SALE', 'CHECK_ITEMS_ON_BOT', 'GET_ITEMS_LIST_NOW', "GET_ITEMS_INFO_BY_ID", "UPDATE_PICE_ITEM"]),
      /*------------------------| ОТКРЫТИЕ ОКНА ПРЕДМЕТА |-------------------------------- */
     async pastMinPriceItem(pClass, pInstance){
      var data = null;
      for (let i = 0; i < this.itemsListNow.length; i++) {
          // data =  this.pastMinPriceItem(this.itemsListNow[i].classid, this.itemsListNow[i].instanceid);
          data = await this.GET_ITEMS_INFO_BY_ID({class: this.itemsListNow[i].classid, instance: this.itemsListNow[i].instanceid});
          if (data) {
            this.itemsListNow[i].market_hash_name = data.market_name;
            this.$set(this.itemsListNow[i], 'CURRENT_PRICE', data.min_price);
          }
          data = null;
        }
      return this.itemsListNow;
    },  
    ///////////////////////////////////////////////////////////////////////////////////////  
      async edititem(pItem){
        this.dialogEdit = true;
        // setInterval(() => {
        //   console.log( pItem.market_hash_name);
        // }, 5000);
        this.loadingTable = true;
        await this.GET_ITEMS_INFO_BY_ID({class: pItem.classid, instance: pItem.instanceid});
        this.loadingTable = false;
      },
      async saveEditItem(){
        console.log(this.currentItemInfo);
        localStorage.setItem(`${this.currentItemInfo.classid+'_'+this.currentItemInfo.instanceid}`,JSON.stringify({min: this.currentItemInfo.minPrice, max: this.currentItemInfo.maxPrice, updatePrice: this.currentItemInfo.updatePrice}));
        // this.$cookies.set(`${this.currentItemInfo.classid+'_'+this.currentItemInfo.instanceid}`,JSON.stringify({min: this.currentItemInfo.minPrice, max: this.currentItemInfo.maxPrice}), 120 );
        this.dialogEdit = false;
        this.addItemsListNow();
        // localStorage.`classid_instanceid` = {min: 4, max: 6};
      },
      async getInventoryItem(){
        this.loadingTable = true;
        this.GET_ITEMS_INVENTORY();
        this.loadingTable = false;
      },
      addItemInSale(){
        // this.ADD_ITEM_IN_SALE({cost: 5000, itemId: 50005511});
        this.GET_ITEMS_ON_SALE();
      },  
      async addItemsListNow(){
        this.loadingTable = true;
        this.GET_ITEMS_LIST_NOW();
        this.loadingTable = false;

      },
      async updatePriceItems(){
        setInterval(() => {
          this.tempArr = []
          for (const item of this.itemsListNow) {
            this.updatePriceItem(item);
          }
          setTimeout(() => {
            this.addItemsListNow();
          }, 2000);
        }, 60000);
      },
     async updatePriceItem(pItem){
        if (this.tempArr.findIndex( item => item == `${pItem.classid}_${pItem.instanceid}`) == -1) {
          this.tempArr.push(`${pItem.classid}_${pItem.instanceid}`)
          if (pItem.UPDATE_PRICE && pItem.MIN_PRICE) {
            await this.UPDATE_PICE_ITEM(
            {
              min: pItem.MIN_PRICE, 
              max: pItem.MAX_PRICE, 
              itemId: pItem.item_id, 
              class: pItem.classid, 
              instance: pItem.instanceid, 
              price: pItem.price
            });
          }
        }
      },
    }
    

  }
</script>
